'use strict';
module.exports = angular.module("app.home").controller("homeCtrl",['homeServiceData','$rootScope','$scope', function(homeServiceData,$rootScope,$scope) {
    const that = this;
    $scope.abc=123;
    that.test = function() {
        console.log(this.name);
        console.log(homeServiceData.mark);
    }
    console.log(that);
    console.log($scope);
    console.log($rootScope);
    setTimeout(function(){
        $rootScope.$emit('mark','hello mark');
    },5000)
}]).service('homeServiceData',[function(){
    return {
        mark:'12123456789'
    }
}]).name;

console.log(module.exports)



